import { _decorator, Component, Button, Sprite, Label } from 'cc';
const { ccclass, property } = _decorator;
import { ChipEntry } from './ChipEntry';
import { EventManager } from '../Managers/EventManager';
import { GameEvent } from '../enums/GameEvent';


@ccclass('ChipButton')
export class ChipButton extends Component {
    private chipEntry: ChipEntry;

    public setup(entry: ChipEntry) {
        this.chipEntry = entry;
        this.node.getComponent(Sprite).spriteFrame = entry.sprite;
        this.node.getComponentInChildren(Label).string = entry.value.toString();
        this.node.on(Button.EventType.CLICK,    () => {
            EventManager.instance.gameEvents.emit(GameEvent.CHIP_SELECTED, entry);
        }, this);
    }
}